<template>
  <div>
    <div class="jgTab">
    <div class="jieGou">
        <div class="jieGou_tit">组织结构</div>
          <el-table
            :data="tableDatareRE"
            stripe
            style="width: 100%">
            <el-table-column
              prop="username"
              label="用户名"
              width="100">
            </el-table-column>
            <el-table-column
              :filters="[{ text: '客服', value: '客服' }, { text: '运维', value: '运维' }, { text: '运营', value: '运营' }]"
              :filter-method="filterTag"
              filter-placement="bottom-end"
              label="部门">
                <template slot-scope="scope">
                    {{scope.row.u_type}}
                </template>
            </el-table-column>
            <el-table-column
              width="100"
              label="操作">
              <template slot-scope="scope">
                  <!-- scope.row.name -->
                  <el-button @click.native="Fnsignproject(scope.row.user_id)" size="small">绑定</el-button>
              </template>
            </el-table-column>
          </el-table>
    </div>
    <!-- 这是绑定的人员数据表 -->
    <div class="tablebox">
        <div class="jieGou_tit">已绑定的用户</div>
        <el-table
        :data="tableD"
        stripe
        style="width: 100%">
        <el-table-column
            prop="username"
            label="用户名">
        </el-table-column>
        <el-table-column
            prop="phone_numbers"
            label="手机">
        </el-table-column>
        <el-table-column
            label="操作">
              <template slot-scope="scope">
                  <el-button @click.native="Fnunsignproject(scope.row.user_id)" size="small">取消绑定</el-button>
              </template>
        </el-table-column>
        </el-table>
    </div>
    </div>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
export default {
    data(){
      return{
        data: [],
        tableD:[],//这是绑定的人员数据表
        tableDatareRE:[]
      };
    },
    methods:{
      Fnemployeelist(){//员工列表
        url.employeelist({
          user_id:this.buserInfo.user_id
        }).then(res=>{
          console.log(res,'---------后台人员列表')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.user_id=element.pk
          newlist.push(element.fields)
          });
          this.tableDatareRE=newlist
        })
      },
      filterTag(value, row) {//row.name---其中name和prop绑定的名字相同
        return row.u_type === value;
      },
      Fnsignproject(Puser_id){//绑定用户操作
        console.log(this.$route.query,'---------------绑定用户操作')
        url.signproject({
          "project_id": this.$route.query.project_id,
          "company_id": this.$route.query.company_id,
          "user_id": this.buserInfo.user_id,
          "p_o_id": Puser_id
        }).then(res=>{
          console.log(res,'-------------这是绑定用户操作')
          this.Fnresponserlist()//关联用户列表
        })
      },
      Fnresponserlist(){//关联用户列表
        url.responserlist({
          "project_id": this.$route.query.project_id,
          "user_id": this.buserInfo.user_id,
        }).then(res=>{
          console.log(res,'--------关联用户列表')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.user_id=element.pk
          newlist.push(element.fields)
          });
          this.tableD=newlist
        })
      },
      Fnunsignproject(Puser_id){//取消用户与项目关联
        url.unsignproject({
          "project_id": this.$route.query.project_id,
          "company_id": this.$route.query.company_id,
          "user_id": this.buserInfo.user_id,
          "p_o_id": Puser_id
        }).then(res=>{
          this.Fnresponserlist()//关联用户列表
          console.log(res,'------------取消用户与项目关联')
        })
      }
    },
    computed:{
      ...mapState(['buserInfo'])
    },
    mounted(){
      this.Fnemployeelist()
      this.Fnresponserlist()//关联用户列表
    }
}
</script>

<style lang="stylus" scoped>
  .jgTab
    display flex
    margin 30px
    .jieGou
      width 300px
    .jieGou_tit
      margin-bottom 17px
    .tablebox
      flex 1
      margin-left 10px
</style>


